}

.card {
    width: 25%;
    /* margin: 20px; */
    /* padding: 5px; */
    /* margin: 0px 14px; */
    margin-right: 20px;
    display: flex;
    position: relative;
    flex: 1 1;
    flex-grow: 1;
}

.card:first-child {
    margin-left: 20px;
}

.card:last-child {
    margin-right: 20px;
}

.detailCard {
    display: flex;
    flex-direction: column;
    background-color: green !important;
    font-size: 15px;
    font-family: Arial, Helvetica, sans-serif;
    box-shadow: rgba(0, 0, 0, 0.3) 0px 19px 38px,
        rgba(0, 0, 0, 0.22) 0px 15px 12px;
    flex: 1 1;
    flex-grow: 1;
}

.ant-card-body {
    flex: 1 1;
}

.chart {
    width: 100%;
}

.result {
    font-size: 20px;
    font-weight: bold;
    color: green;
}

.ant-card-head-title {
    font-size: 20px;
    color: blue;
}

.titleChart {
    text-align: center;
    font-size: 21px;

    height: -webkit-fit-content;

    height: -moz-fit-content;

    height: fit-content;
    width: 100%;
    margin: auto;
}

canvas {
    height: 100% !important;
}

.nameTittle {
    display: inline;
    background: linear-gradient(to right, #30cfd0 0%, #330867 100%);
    border-radius: 10px;
    color: transparent;
    -webkit-background-clip: text;
            background-clip: text;
    font-weight: 1000;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}

