.chartAntd {
    /* background-color: #fff; */
    margin: 0px 20px;
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    flex-wrap: nowrap;
}

.chart {
    border-radius: 10px;
    margin-top: 20px;
    margin-right: 20px;
    height: "100%";
    width: 50%;
    padding: 10px;
    position: "relative";
    background-color: #fff;
    box-shadow: rgba(0, 0, 0, 0.3) 0px 19px 38px,
    rgba(0, 0, 0, 0.22) 0px 15px 12px;
}
.chartDPU {
    border-radius: 10px;
    margin-top: 20px;
    margin-right: 40px;
    /* height: "90%"; */
    width: calc(100vw - 40px);
    padding: 10px;
    position: "relative";
    background-color: #fff;
    box-shadow: rgba(0, 0, 0, 0.3) 0px 19px 38px,
    rgba(0, 0, 0, 0.22) 0px 15px 12px;
}
.chart:last-child{
    margin-right: 0px;
}
.legendChart {
    font-size: 20px;
}

.row {
    padding-top: 10px;
}

